body, div, h1, h2, h3, h4, h5, h6, p, ol, ul, blockquote, figure{
  margin:0;
  padding:0;
}
html {
  scroll-behavior: smooth;
}

body{
  position: relative;
  font-family: 'Alegreya', serif, Ubuntu, sans-serif;
  max-width: 2500px;
  margin:0 auto;
  overflow-x: hidden;
  color: var(--textColor);
  background-color: #593D3B;
}

img{
  width: 100%;
  height: auto;
  display: block;
}

a{
  text-decoration: none;
  color: inherit;
}

ul, ol{
  list-style: none;
}

address{
  font-style: normal;
}

html {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}
*, *:before, *:after {
  -webkit-box-sizing: inherit;
  -moz-box-sizing: inherit;
  box-sizing: inherit;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #25140D;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body > .filters-wrapper:last-of-type{
  margin-top: 1rem;
  border:1px solid;
}

.filters{
  width: calc(100% + 1rem);
  margin-bottom: 1rem;
}

.filters ul{
  display:-webkit-flex;
  display:flex;
  margin-bottom:.5rem;
  max-width:100%;
  overflow-x:scroll;
  -ms-scroll-snap-type:x mandatory;
  scroll-snap-type:x mandatory;
  -ms-overflow-style:none;
  scrollbar-width:none
}

.filters ul::-webkit-scrollbar{
  display:none
}

.filters ul li{
  border:1px solid #fff;
  color: #fff;
  padding:2px 5px;
  font-size:12px;
  scroll-snap-align:start;
  flex-shrink: 0;
}

.filters ul li.active{
  background-color:#fff;
  color:#25140D;
}

.filters ul li:not(:last-of-type){
  margin-right:.5rem;
}
.filters ul li:last-of-type{
  margin-right:1rem;
}


.intro{
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 200;
  background-color: #25140D;
  transition: left 0.3s;
}

.intro.hide{
  left: -200vw;
}

.intro-wrapper{
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  flex-direction: column;
}

.intro .logo{
  width: 180px;
  margin: 0 auto;
  opacity: 0;
  transition: opacity 0.3s;
}

.intro .logo.show{
  opacity: 1;
}

.intro .btn{
  width: 250px;
  margin: 4rem auto 0;
  padding: 0.5rem 1rem;
  border:1px solid #fff;
  box-shadow: 2px 2px 5px #000;
  color: #fff;
  text-align: center;
  border-radius: 15px;
  background-color: #3C2015;
  opacity: 0;
  transition: opacity 0.3s;
}

.intro .btn.show{
  opacity: 1;
}