.ProductListItem{
    /*
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    background-color: rgba(255,255, 255, 0.2);
    */
    margin-bottom: 12px;
    color: #fff;
}

.ProductListItemImage{
    width: calc(100% + 16px);
    height: 250px;
    /*margin-bottom: 1rem;*/
    left: -8px;
    position: relative;
}

.ProductListItemImage img{
    object-fit: cover;
    object-position: center;
    width: inherit;
    height: 100%;
}

.ItemInfo{
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    border-bottom: 1px solid #a1a1a1;
}

.ProductListItemName{
    max-width: calc(100% - 12ch);
    font-size: 15px;
    line-height: 20px;
    text-align: left;
    position: relative;
    font-weight: 300;
    letter-spacing: 0.22px;
}

.ProductListItemName img{
    position: absolute;
    width: 60px;
    top: 0;
    left: 95%;
    z-index: 5;
}




