.ProductListItemBottle{
   /* width: 100%;
    text-align: right;*/
    font-size: 14px;
    font-style: italic;
    position: relative;
    padding-left: 3px;
    margin-left: 5px;
    font-weight: 300;
    display: flex;
    flex-direction: column;
    border-left: 1px solid;
}
.ProductListItemBottle:before{
    /*
    content: "φιάλη";
    font-size: 10px;
    position: absolute;
    bottom: -8px;
    left: 12px;
    */
}

.ProductListItemBottle span{
    font-size: 10px;
}

.ProductListItemDescription{
    width: 100%;
    font-size: 11px;
    padding-left: 1.5rem;
    position: relative;
    padding-top: 2px;
    text-align: right;
}

.ProductListItemDescription:before{
    content: '';
    position: absolute;
    width: 100%;
    border-top: 1px solid var(--priceColor);
    left: 0;
    top: 0;
}