.FooterWrapper{
    max-width: 450px;
    margin: 0 auto;
}

.Address{
    display: block;
    text-align: right;
    align-items: center;
    position: relative;
}

.Address address{
    font-style: normal;
    font-weight: 100;
    font-size: 12px;
    border-top: 1px solid #fff;
}

.Footer address span{
    display: block;
}

.Logo{
    width : 70px;
    padding: 1rem;
}

.Footer h3{
    font-weight: 100;
}

.SocialWrap{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding: 1rem;
    align-items:  center;
}

.SocialWrap h3{
    font-weight: 300;
}
.SocialWrap h4{
    font-weight: 100;
    border-top: 1px solid #fff;
    font-size: 14px;
    letter-spacing: 1.4px;
}

.Social{
    display: flex;
    justify-content: flex-end;
    width: 100px;
    margin: 0 auto;
    filter: invert(1);
    padding-bottom: 5px
}

.Social li{
    width: 16px;
}

.Social li:first-of-type{
    width: 8px;
    margin-right: 1.5rem;
}

.PhonesWrapper{
    padding: 10px 20px;
    background-color: #F5B700;
    color: #000;
}

.PhonesWrapper h3{
    padding-top: 0;
    font-size: 20px;
}
.Phones{
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    margin-top: 1rem;
}

.Phones > div{
    display: flex;
    flex-direction: column;
    font-size: 10px;
    line-height: 12px;
}
.Phones > div a{
    font-size: 18px;
    line-height: 20px;
    letter-spacing: 0.65px;
}

.Developer{
    font-size: 12px;
    padding: 0.5rem 0;
    position: relative;
    font-weight: 100;
    display: flex;
    justify-content: center;
    align-items: flex-end;
    background-color: #0F0805;
    color: #fff;
}

.Developer h6{
    margin-left: 1ch;
}

.Developer h6{
    font-size: 16px;
    font-weight: 100;

}

