.CategoryTab{
    /*border:2px solid #FFF;*/
    width: 100%;
    aspect-ratio: 1;
    overflow: hidden;
    position: relative;
}

.CategoryTab:before{
    content : '';
    width: 3rem;
    height: 3rem;
    background: #25140D;
    position: absolute;
    left: -1.5rem;
    top:-1.5rem;
    z-index: 120;
    transform: rotate(45deg);
}
.CategoryTab:after{
    content: '';
    left: 0;
    bottom: 0;
    position: absolute;
   /* background: linear-gradient(0deg, rgba(0,0,0,1) 0%, rgba(0,0,0,0) 35%);*/
   background-color: rgba(0,0,0,0.1);
    width: 100%;
    height: 100%;
}

.CategoryTabLG{
    border:5px solid #fff;
    width: 100%;
    margin-bottom: 10px;
    height: 110px;
    overflow: hidden;
    position: relative;
}

.CategoryTabLG:after{
    content: '';
    left: 0;
    bottom: 0;
    position: absolute;
   /* background: linear-gradient(0deg, rgba(0,0,0,1) 0%, rgba(0,0,0,0) 35%);*/
   background-color: rgba(0,0,0,0.1);
    width: 100%;
    height: 100%;
}


.LinkWrapper{
    width: 100%;
    height: 100%;
}
.CategoryInnerWrapper{
    width: 100%;
    height: 100%;
    display: flex;
    align-items: flex-end;
}

.TabImg{
    /*
    width: 100%;
    height: 100%;;
    position: absolute;
    left:0;
    top: 0;
    */
    width: 100%;
    height: 100%;
}

.TabImg img{
    object-fit: cover;
    object-position: center;
    width: inherit;
    height: inherit;
}

.CategoryName{
    /*
    display: flex;
    align-items: flex-end;
    justify-content: flex-start;
    width: 100%; 
    height: 100%;
    */
    position: absolute;
}

.CategoryTab h2, .CategoryTabLG h2{
    position: relative;
    z-index: 2;
    color: #fff;
    padding: 5px 10px;
    font-weight: 400;
    font-size: 14px;
    letter-spacing: 1.2px;   
}

.CategoryTab h2:before{
    content: "";
    width: 100%;
    height: 100%;
    z-index:-1;
    position: absolute;
    left: 0;
    bottom: 0;
    text-shadow: 2px 2px 2px #141115;
    background-color: #25140D; 
    box-shadow: 2px 2px 3px #000; 
}

@media screen and (max-width: 400px) {
    .CategoryTab h2{
        padding: 5px;
        letter-spacing: 1.5px;
    }
}

