.Category{
    padding: 0 1rem;
}
.Category h2{
    margin-bottom: 1rem;
    font-weight: 300;
    font-size: 24px;
    /*
    color: #fff;
    display: inline-block;
    width: min-content;
    */
    color: #FFF;
    text-align: left;
    border-bottom: 4px solid #fff;
    padding-bottom: 2px;  
    letter-spacing: 2.5px;  
}


.CategoryHeader{
    color: #000;
    margin-bottom: 1rem;
    padding:10px 2.5rem;
    border-radius: 20px 20px 0 0;
    background-color: #fff;
}
.CategoryPreFooter{
    color: #000;
    background-color: #fff;
    margin: 10px 0 1rem;
    font-size: 16px;
    padding: 8px 12px;
    border: 1px solid;
    text-align: left;
}
.CategoryFooter{
    color: #fff;
    margin: 10px 0 1rem;
    font-size: 12px;
    padding: 8px 12px;
    border: 1px solid;
    text-align: left;
    border-radius: 0 20px 0 0;
}

.PriceBtns{
    display: flex;
    margin-bottom: 1rem;
    align-items: center;
    color: #fff;
    border:1px solid;
    border-radius: 5px;
    background-color: #104547;
    padding: 8px 5px;
    justify-content: flex-end;
}

.PriceBtns p{
    margin-right:auto;
}

.PriceBtns button{
    padding: 10px 5px;
    outline: 0;
    font-size: 12px;
    transition: background 0.5s;
    border-radius: 4px;
}

.PriceBtns button:first-of-type{
    margin-right: 10px;
}

.TakeAwayBtn{
    border:1px solid #2B954A;
    /*margin: 1rem auto;*/
}
.TakeAwayBtnActive{
    background-color: #2B954A;
    border:1px solid #2B954A;
    color: #fff;
}

.ShopBtn{
    border:1px solid #931A1F;
}
.ShopBtnActive{
    background-color: #931A1F;
    border:1px solid #931A1F;
    color: #fff;

    /*margin: 1rem auto;*/
}

.Back{
    padding-bottom: 1rem;
    padding: 1rem 0 2rem;
}

.Back a{
    border: 1px solid #fff;
    padding: 0.5rem 1rem;
    background: #BC471B;
    color: #fff;
    font-size: 14px;
}

.Back a:first-of-type{
    border-right: none;
}
.Back a:last-of-type{
    border-left: none;
}

.Back span{
    font-size: 14px;
    position: relative;
    top: 2px;
}


.TopNavigation{
    position: fixed;
    top: 24px;
    left: 16px; 
    z-index:10;
    width: 22px;   
    border:1px solid #fff;
    background-color: #25140D;
}

.TopNavigation  div{
    filter: invert(1);
    padding: 0.25rem;
}


.Category aside{
    color: #fff;
    font-size: 10px;
    text-align: right;
}