.Menu{
  padding: 0 1rem;
}
.MenuGrid{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  

  .Menu > h2{
    color: #fff;
    font-weight: 300;
    margin-bottom: 1rem;
    border-bottom:4px solid;
    padding-bottom: 2px;
    text-align: left;
    font-size: 22px;
  }

  .GridItem{
    width: calc((100% - 1rem) / 2);
    margin-bottom: 10px;
  }

  .Manager{
    background-color: #808275;
    color: #fff;
    margin-bottom: 10px;
  }

  .Manager h4{
    font-weight: 300;
    text-align: center;
    padding: 5px;
  }

  .Manager h4 span{
    display: block;
  }

  .Alert{
    color: #fff;
    margin-bottom: 10px; 
    padding: 6px 12px; 
    text-align: right;
  }

  .Alert h5{
    margin:0 auto;
    border: 1px solid #fff;
    font-weight: 300;
    padding: 6px;
    font-size: 12px;
  }