.ProductSubList:not(:last-of-type){
    margin-bottom: 2rem;
}
.ProductSubList h4{
    font-size: 20px;  
    text-align: left;
    margin-bottom: 1rem;
    width: fit-content;
    color: #fff;
    letter-spacing: 0.5px;
    font-weight: 300;
    background-color: #fff;
    padding: 0 5px;
    color: #25140D;
    font-weight: 300;
    margin-left: auto;
}

.ProductSubList h4:not(:first-of-type){
    font-size: 17px;
    background-color: transparent;
    color: #fff;
    padding: 0;
    margin-top: -1rem;
    position: relative;
    text-align: right;
    border-bottom: 1px solid #fff;
}


.ProductSubList h5{
    background-color: #fff;
    color: #25140D;
    font-weight: 300;
    text-align: left;
    margin-bottom: 0.5rem;
    padding:5px;
}
.ProductSubList h4 span,
.ProductSubList h5 span{
    display: block;
}